.mint-this-nft {
  font-family: Founders Grotesk, Arial Black, sans-serif;
  line-height: 1;
  text-transform: uppercase;
  text-shadow: 0.5px 0.5px #666, 0.5px 0.5px #000;
  font-weight: bold;
  color: #0f172a;
  background-color: #f6d860;
}

.nft {
  @apply m-0 relative;
}

.overlay {
  @apply py-3 px-2 absolute w-full bottom-0 opacity-0 text-bnft-gray;
  background: rgba(0, 0, 0, 30%);
  transition: 0.5s ease;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.nft:hover .overlay {
  opacity: 1;
}
